import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let state = {
  tab_list: []
}
let getters = {}
let mutations = {}
for (let key in state) {
  mutations['update__' + key] = (state, data) => {
    state[key] = data
  }
  getters[key]=state=> state[key]
}
console.log('mutations', getters);

export default new Vuex.Store({
  state: state,
  getters:getters,
  mutations: mutations,
  actions: {

  }
})
