<template>
    <div class="cont">
        z准备的树型结构
       
    </div>
</template>

<script>
    import router from '../router.js'
    export default {
        data() {
            return {
                routes_add: [
                {
                    path: '/add',
                    name: 'Add',
                    // route level code-splitting
                    // this generates a separate chunk (about.[hash].js) for this route
                    // which is lazy-loaded when the route is visited.
                    component: 'Add'
                    // children:[
                    //     {
                    //         path: '/childrenadd',
                    //         name: 'ChildrenAdd',
                    //         // route level code-splitting
                    //         // this generates a separate chunk (about.[hash].js) for this route
                    //         // which is lazy-loaded when the route is visited.
                    //         component: 'a/B',
                    //     }
                    // ]
                }, {
                    path:'/a/B.vue',
                    name: 'B_add',
                    component: 'a/B'
                }
                ]
            }
        },
        methods: {
            addRoutes() {
                console.log('添加路由')
                let addrouter = this.routes_add
                let routes = []
                this.generaMenu(routes, addrouter) // 将后台的路由数据components转化成组件          
                router.addRoutes(routes) // 添加路由
                router.push('/')  // 调到首页
                // this.$router
            },
            lazy(name) {
                // let path = '';
                // let file = '';
                // if (name.indexOf('/') == -1) {
                //     file = name;
                // } else {
                //     path = name.split('/')[0];
                //     file = name.split('/')[1];
                // }
                // if (name == 'Home' || name == 'Login' || (name.indexOf('/') == -1)) {
                //     return () => import(`../views/${file}.vue`)
                // } else {
                //     return () => import(`../views/${path}/${file}.vue`)
                // }\
                
                return () => import(`../views/${name}.vue`)
            },
            generaMenu(routers, data) {
                data.forEach((item) => {
                    let menu = Object.assign({}, item);
                    console.log('menu菜单',menu, )
                    //menu.component = import(`../views/${menu.component}.vue`)  这样加载有问题 添加lazy加载
                    // let comp=menu.component
                    // menu.component=function(){
                    //     return () => import(`../views/${name}.vue`)
                    // }                   
                    menu.component = this.lazy(menu.component);
                    if (item.children) {
                        menu.children = []
                        this.generaMenu(menu.children, item.children)
                    }
                    routers.push(menu)
                })
            }
        },
        mounted() {
            // this.addRoutes()
        }
    }
</script>
<style scoped>
    @media screen and (min-width: 992px) {
        .cont {
            font-size: 18px;
        }
    }

    @media screen and (max-width:768px) {
        .cont {
            font-size: 12px;
        }
    }
</style>