<template>
    <div class="aside">
        <ul class="menu">
            <li class="lev1" :ref="lev1_index" v-for="(lev1, lev1_index) in list" :key="lev1_index">
                <div @click="expand(lev1,lev1_index)">{{lev1.name}}</div>
                <ul v-if="lev1.children" :ref="lev1_index">
                    <li
                        class="lev2"
                        :ref="lev1_index+'-'+lev2_index"
                        v-for="(lev2, lev2_index) in lev1.children"
                        :key="lev2_index"
                    >
                        <div
                            :class="[$route.path==lev2.path?'act-title':'']"
                            @click="expand(lev2,lev1_index+'-'+lev2_index)"
                        >
                            {{lev2.name}}
                            <span v-if="lev2.children">+</span>
                            <ul v-if="lev2.children" :ref="lev1_index+'-'+lev2_index">
                                <li v-for="(lev3, lev3_index) in lev2" :key="lev3_index">
                                    {{lev3.name}}
                                </li>
                            </ul>
                        </div>
                    </li>
                </ul>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";
export default {
    data() {
        return {
            list: [
                {
                    name: "一",
                    //   path: "/userlist",
                    children: [
                        { name: "11", path: "/about" },
                        { name: "12", path: "/tree" }
                    ]
                },
                {
                    name: "二",
                    //   path: "/userlist",
                    children: [
                        { name: "21", path: "/add" },
                        { name: "22", path: "/hedfh" },
                        {
                            name: "23",
                            // path: "/a/b",
                            children: [
                                { name: "231", path: "/about" },
                                { name: "232", path: "/tree" }
                            ]
                        }
                    ]
                },
                {
                    name: "三",
                    //   path: "/userlist",
                    children: [
                        { name: "31", path: "/add" },
                        { name: "32", path: "/hedfh" },
                        { name: "33", path: "/a/b" }
                    ]
                }
            ],
            no_watch: true
        };
    },
    computed: {
        ...mapGetters(["tab_list"])
        // ...mapState["tab_list"],
    },
    methods: {
        ...mapMutations(["update__tab_list"]),
        is_father(father, child) {
            console.log("a😍");
            let isFather = false;
            if (typeof child == "string") {
                child = { path: child };
            }
            if (!Array.isArray(father)) {
                if (father.path === child.path) {
                    return (isFather = true);
                } else {
                    return false;
                }
            }
            father.forEach((lev1, lev1_index) => {
                if (lev1.path == child.path) {
                    isFather = true;
                    if (lev1.children) {
                        // return this.is_father(lev1.children, child)
                    } else {
                    }
                }
            });
            return isFather;
        },
        expand(menu, index) {
            if (menu.children) {
                // 打开或关闭当前
                let dom = this.$refs[index][0];
                if (dom.offsetParent === null) {
                    window.slide.slideDown(dom, 200);
                } else {
                    window.slide.slideUp(dom, 200);
                }
                // 关闭其他同级菜单--------------
                this.list.forEach((lev1, lev1_index) => {
                    // 不是其父级,也不是他自己就关闭
                    // console.log('父级‘',lev1)
                    // console.log('子集‘',menu)
                    
                    // 如果点击的是一级菜单 。

                    if (lev1.name != menu.name) {
                        console.log("le1name", lev1, name);
                        console.log("menu", menu.name);
                        // if(lev1.children){
                        //     lev1.children.forEach((lev2.))
                        // }
                        let dom = this.$refs[lev1_index][0];
                        window.slide.slideUp(dom, 200);
                    } else {
                        console.log("le1name----", lev1.name);
                        console.log("menu----", menu.name);
                    }
                });

                /* 没有子菜单********************** */
            } else {
                this.$router.push(menu.path);
                // 点击的跳转 不watch
                this.no_watch = false;
                // let list = this.$store.state.tab_list;
                let list = this.tab_list;
                if (!list.some(item => item.path === menu.path)) {
                    list.push(menu);
                    this.update__tab_list(list);
                }

                // 关闭同级其他
            }
        }
    },
    watch: {
        $route(to, from) {
            // console.log(to,'to')
            // // 点击进来的不需要watch，
            if (!this.no_watch) {
                this.no_watch = true;
                return;
            }

            let current_path = to.path;
            // 如果 父级是关闭，那么就打开他，
            // 获取父级
            this.list.forEach((lev1, lev1_index) => {
                if (lev1.path === to.path) {
                }

                if (lev1.children) {
                    lev1.children.forEach((lev2, lev2_index) => {
                        if (lev2.path === to.path) {
                            // 如果父级没有打开，那么打开它
                            let parent_dom = this.$refs[lev1_index][0];
                            // console.log("parent_dom", parent_dom);
                            if (parent_dom.offsetParent === null) {
                                let parent_dom = this.$refs[lev1_index][0];
                                window.slide.slideDown(parent_dom);
                            } else {
                                // window.slide.slideUp(parent_dom, 200);
                            }
                        }
                    });
                    // 是否是其父级
                    let father = this.is_father(lev1.children, to.path);
                    if (!father) {
                        let parent_dom = this.$refs[lev1_index][0];
                        console.log("父亲，dom", parent_dom);
                        window.slide.slideUp(parent_dom, 200);
                    }
                }
            });
        }
    },
    mounted() {}
};
</script>

<style scoped>
.aside {
    user-select: none;
}
.menu ul {
    display: none;
}
.menu li > div {
    width: 100%;
    border-bottom: 1px solid #000;
    background: rgb(123, 162, 212);
    color: #fff;
}
.menu li > div:hover {
    background: rgb(149, 187, 236);
}
.menu li > div:active {
    background: rgb(123, 152, 190);
    color: #fff;
}
.menu .lev1 > div {
    padding: 6px 10px;
}
.menu .lev2 > div {
    padding: 6px 10px 6px 40px;
    border: 1px solid #000;
}
.act-title {
    border-top: 1px solid rgb(58, 62, 66);
    border-left: 2px solid rgb(55, 62, 71);
}
</style>