const r = require.context('../../views',true, /\.vue$/, 'lazy')
let route =[]
r.keys().forEach(filename=>{
  let path = filename.toLowerCase().slice(1).replace('.vue','')
  console.log('内容',path);
  
  let length =filename.split('/').length;
  let name =filename.split('/')[length-1].replace('.vue','')
  console.log('内容',name);
  
  route.push({
    // name: name,
    path: path,
    // meta:{},

    component: ()=>r(filename)
  })
})
console.log('最终完成',route)
export default route